.products {
  &__content {
    @include page-grid;
    row-gap: 56px;

    @include on-tablet {
      row-gap: 80px;
    }
  }

  &__title {
    text-transform: uppercase;
    margin: 0;
    grid-column: 1 / -1;
    font-size: 48px;
    font-weight: 400;
    line-height: 40.8px;
    letter-spacing: -0.05em;
    margin-bottom: 24px;

    @include on-tablet {
      font-size: 56px;
      line-height: 47.6px;
    }

    @include on-desktop {
      font-size: 72px;
      line-height: 61.2px;
    }
  }

  &__desc {
    margin: 0;
    grid-column: 1 / -1;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.01em;
    margin-bottom: 56px;

    @include on-tablet {
      margin-bottom: 80px;
    }
  }

  &__set {
    @include page-grid;

    grid-column: 1 / -1;
    row-gap: 56px;

    @include on-tablet {
      row-gap: 80px;
    }
  }
}
