.header {
  &__main {
    position: relative;
    @include page-grid;
  }

  &__desc {
    margin: 0;
    grid-column: 1 / -1;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.01em;
    text-align: center;
    margin-block: 32px;

    @include on-tablet {
      display: flex;
      width: 280px;
      text-align: left;
      margin-block: 80px 146px;
    }

    @include on-desktop {
      margin: 0;
      width: 100%;
      grid-column: 1 / 3;
      margin-block: 135px 8px;
    }

    @include on-desktopxl {
      margin-block: 144px 40px;
    }
  }

  &__title {
    margin: 0;
    grid-column: 1 / -1;
    font-size: 64px;
    font-weight: 400;
    line-height: 54.4px;
    letter-spacing: -0.05em;
    text-align: center;

    @include on-tablet {
      z-index: 2;
      font-size: 80px;
      line-height: 68px;
    }

    @include on-desktop {
      font-size: 136px;
      line-height: 115.6px;
    }

    @include on-desktopxl {
      font-size: 144px;
      line-height: 122.4px;
    }
  }

  &__img {
    grid-column: 1 / -1;
    width: 100%;

    @include on-tablet {
      position: absolute;
      top: 144px;
      margin: 0 50%;
      transform: translateX(-50%);
      width: 326px;
      height: 328px;
    }

    @include on-desktop {
      top: 0;
      width: 544px;
      height: 546px;
    }

    @include on-desktopxl {
      width: 584px;
      height: 586px;
    }
  }

  &__link {
    grid-column: 1 / -1;
    display: flex;
    gap: 4px;
    height: 56px;
    width: 100%;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: $main-color;
    background-color: $main-bg-color;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.02em;
    border: 2px solid $main-color;
    border-radius: 32px;
    margin-bottom: 40px;

    @include hover(transform, scale(1.05));

    @include on-tablet {
      grid-column: 3 / 7;
      margin-block: 210px 228px;
    }

    @include on-desktop {
      grid-column: 1 / 3;
      grid-row: 4 / 5;
      margin-block: 64px 161px;
    }

    @include on-desktopxl {
      margin-bottom: 138px;
    }
  }
}
