.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 64px;

  padding-inline: 16px 20px;

  @include on-desktop {
    padding-inline: 32px;
    height: 88px;
  }

  &__logo {
    @include hover(transform, scale(1.05));
  }

  &__nav {
    display: none;

    @include on-desktop {
      display: flex;
    }
  }

  &__list {
    gap: 64px;
    align-items: center;
  }

  &__link {
    color: $secondary-color;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.01em;

    display: inline-block;
    height: 24px;

    @include hover(transform, scale(1.05));
  }

  &__menu {
    @include on-desktop {
      display: none;
    }
  }
}
