.product {
  grid-column: 1 / -1;

  @include hover(transform, scale(1.05));

  &--left {
    @include on-tablet {
      grid-column: 1 / 5;
    }
  }

  &--right {
    @include on-tablet {
      grid-column: 5 / -1;
    }
  }

  &--left-left {
    @include on-desktop {
      grid-column: 1 / 4;
    }
  }

  &--left-right {
    @include on-desktop {
      grid-column: 4 / 7;
    }
  }

  &--right-left {
    @include on-desktop {
      grid-column: 3 / 6;
    }
  }

  &--right-right {
    @include on-desktop {
      grid-column: 6 / -1;
    }
  }

  &__img {
    width: 100%;
  }

  &__title {
    text-transform: uppercase;
    margin: 0;
    color: $footer-title-color;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.02em;
    margin-block: 16px 8px;
  }

  &__desc {
    margin: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.01em;
    margin-bottom: 16px;
    display: flex;
    height: 72px;
  }

  &__order {
    text-transform: uppercase;
    display: flex;
    gap: 4px;
    align-items: center;
    color: $main-color;
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.02em;

    @include hover(transform, scale(1.05));
  }
}
