.menu {
  &__top-bar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }

  &__list {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
  }

  &__link {
    color: $secondary-color;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.01em;

    display: inline-block;
    height: 24px;
  }
}
